<template>
  <div class="create-post">
    <Panel>
      <template #header>
        <router-link to="/"></router-link><span>/</span
        ><span></span>
      </template>
      <template #content>
        <form action="">
          <div>
            <span>:</span>
            <select v-model="post.tab" name="" id="">
              <!-- <option value="type"></option> -->
              <option value="ask"></option>
              <option value="share"></option>
              <option value="job"></option>
              <option value="dev"></option>
            </select>
          </div>
          <div>
            <input v-model="post.title" type="text" />
          </div>
          <div>
            <!-- md -->
            <!-- mdcontentmdmdhtmldiv.markdown-text -->
            <textarea
              v-model="post.content"
              name=""
              id=""
              cols="30"
              rows="10"
            ></textarea>
          </div>
          <button @click="submit" type="primary"></button>
        </form>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import axios from '../plugins/axios'
export default {
  components: { Panel },
  data() {
    return {
      post: {
        title: "",
        tab: "",
        content: "",
      },
    };
  },
  methods: {
      async submit() {
        //   

        let {tab,title,content} = this.post
        // content = "<div class='markdown-tetx'>"+content+'</div>'

        // token
        const accesstoken = localStorage.getItem('token')
        const res=await axios.post('/topics',{tab,title,content,accesstoken})
        console.log(res);
        this.$router.push(`/post/${res.topic_id}`)


      }
  },
};
</script>

