<template>
  <div v-if="content" class="postContent">
    <Panel>
      <template #content>
        <div class="post-header">
          <!--  -->
          <h2>
            <span class="tag" v-if="content.top || content.good"
              >{{ content.top ? "" : "" }} </span
            >{{ content.title }}
          </h2>
          <div>
            <p>
              <span class="disc"></span>
              <span>{{ content.create_at | moment("from", "now") }}</span>
              <span>{{ content.author.loginname }}</span>
              <span class="disc"></span>
              <span>{{ content.visit_count }}</span>
              <span class="disc"></span>
              <span>{{ content.tab | transformTab }}</span>
            </p>
            <el-button
              @click="collect"
              size="mini"
              :type="content.is_collect ? 'info' : 'success'"
              v-if="login"
              >{{ content.is_collect ? "" : "" }}</el-button
            >
          </div>
        </div>
        <div class="post-content" v-html="content.content"></div>
      </template>
    </Panel>
    <Panel>
      <template #header>
        <span>{{ content.reply_count }}</span>
      </template>
      <template v-if="content.replies.length" #content>
        <PostCommentItem
          v-for="(comment, index) in content.replies"
          :key="comment.id"
          v-bind="{ ...comment, index }"
        />
      </template>
    </Panel>
    <Panel>
      <template #header>
        <span>  </span>
      </template>
      <template #content>
        <!-- md -->
        <!-- <mavon-editor
          defaultOpen="edit"
          :subfield="false"
          :toolbars="toolbars"
        /> -->
        <textarea v-model="contents" cols="30" rows="10"></textarea>
        <el-button type="primary"></el-button>
      </template>
    </Panel>
  </div>
  <el-skeleton v-else :rows="8" animated />
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostCommentItem from "../components/PostCommentItem.vue";

import Panel from "../containers/Panel.vue";
export default {
  components: { Panel, PostCommentItem },
  data() {
    return {
      contents: "",
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true, // 
      },
    };
  },
  created() {
    // idmutions
    this.getPostContent(this.$route.params.id);
  },
  computed: {
    ...mapState({
      content: (state) => state.postContent.content,
      login: (state) => state.login.login,
    }),
  },

  methods: {
    // actions
    ...mapActions(["getPostContent", "collectHandle", "addComment"]),
    collect() {
      this.collectHandle({
        id: this.content.id,
        is_collect: !this.content.is_collect,
      });
    },
    add() {
      if (this.content.trim()) {
        this.addComment({ id: this.content.id, content: this.content });
      }
    },
  },
};
</script>

<style lang="less">
.post-header {
  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .tag {
    font-size: 12px;
    color: #fff;
    background-color: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    margin-right: 7px;
  }
  .disc {
    margin: 0 10px;
    font-weight: bold;
  }
  p {
    font-size: 14px;
    color: #999;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
  }
}
.post-content {
  img {
    width: 100%;
  }
}
</style>