<template>
  <div v-if="user" class="user">
    <Panel>
      <template #header>
        <router-link to="/">/</router-link>
      </template>
      <template #content>
        <div class="user-name">
          <el-avatar
            class="avatar"
            size="medium"
            :src="user.avatar_url"
            shape="square"
          ></el-avatar>
          <span>{{ user.loginname }}</span>
        </div>
        <!--  -->
        <p class="user-score">{{ user.score }}</p>
        <!--  -->
        <a href="#" class="user-topics"
          >{{ user.recent_topics.length }}</a
        >
        <!-- github -->
        <p>
          <a target="_blank" href="`https://github.com`" class="user-github"
            >^_^{{ user.githubUsername }}</a
          >
        </p>
        <!--  -->
        <p class="user-create-time">
          {{ user.create_at | moment("from", "now") }}
        </p>
      </template>
    </Panel>
    <Panel>
      <template #header>  </template>
      <template #content>
        <Tabless v-bind="item" v-for="item in recent_topics" :key="item.id" />
      </template>
    </Panel>
    <Panel>
      <template #header>  </template>
      <template #content>
        <Tabless v-bind="item" v-for="item in recent_replies" :key="item.id" />
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Tabless from "../components/Tabless.vue";
import Panel from "../containers/Panel.vue";
import axios from "../plugins/axios";
export default {
  components: { Panel, Tabless },
  data() {
    return {
      recent_replies: [],
      recent_topics: [],
    };
  },
  computed: {
    ...mapState({
      user: (state) => state.user.user,
    }),
  },
  // created() {
  //   this.getUser(this.$route.params.loginname);
  //   // console.log(this.$route);
  // },
  watch: {
    "$route.params.loginname": {
      handler(newValue) {
        this.getUser(newValue);
      },
      immediate: true,
    },
    // user   
    user: {
      // user
      // 
      // 1.userusernullactionuser
      // 2.
      async handler(newValue) {
        // 
        const res1 = await axios.all(
          newValue.recent_replies.map((topic) =>
            axios.get(`/topic/${topic.id}`)
          )
        );
        this.recent_replies = res1.map((ele) => ele.data);

        const res2 = await axios.all(
          newValue.recent_topics.map((topic) => axios.get(`/topic/${topic.id}`))
        );
        this.recent_topics = res2.map((ele) => ele.data);
      },
    },
  },
  methods: {
    ...mapActions(["getUser"]),
  },
};
</script>

<style lang="less">
.user {
  .panel {
    margin-bottom: 10px;
    .user-name {
      display: flex;
      span {
        font-size: 14px;
      }
      .el-avatar--medium {
        margin-right: 10px;
      }
    }
    .user-score {
      font-size: 14px;
    }
    .user-topics,
    .user-github,
    .user-name span {
      color: #778087;
    }
    .user-create-time {
      color: #ababab;
    }
  }
}
</style>